<template>
  <article class="card-btn">
    <router-link
      class="d-block"
      tag="div"
      :to="{ name: 'productDetail', params: { 'product_id': cardListProp.product_id }}"
    >
      <div class="w-100 mb-3 position-relative img_out_box">
        <div class="w-100 h-100 flex-center position-absolute img_in_box">
          <img class="w-100" :src="productImg">
        </div>
      </div>
      <div class="computer-view d-none d-sm-block">
        <div class="size-group d-flex mb-3 justify-content-center"></div>
        <h6 class="text-center mb-3">{{cardListProp.title}}-{{cardListProp.gender}}</h6>
        <h6 class="text-center">NT${{cardListProp.price}}</h6>
      </div>
      <div class="mobile-view d-block d-sm-none">
        <h6 class="mb-1">{{cardListProp.title}}</h6>
        <h6>${{cardListProp.price}}</h6>
      </div>
    </router-link>
  </article>
</template>
<script>
export default {
  props: ["cardListProp"],
  computed: {
    productImg() {
      return `${process.env.VUE_APP_BACKURL}img/${
        this.cardListProp.product_id
      }/1.jpg`;
    }
  },
  directives: {
    "set-imgbox-height": {
      inserted: (el, binding, vnode) => {
        el.style.height = `${1.6 * el.clientWidth}px`;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.card-btn:hover {
  cursor: pointer;
}
.img_out_box{
  padding-top:150%
}
.img_in_box{
  top:0;
  left: 0;
  overflow-y: hidden;
}
</style>
