<template>
  <aside>
    <div class="mb-3" v-for="(bigCategory,bigIndex) in catrgoryData" :key="bigIndex">
      <h5 class="category-title mb-2 font-weight-bold">。{{bigCategory.bigName}}</h5>
      <router-link
        tag="a"
        class="text-primary d-block ml-2 mb-1"
        v-for="(category,index) in bigCategory['category']"
        :key="index"
        :to="{ name: 'cardList', params: { 'category': category.id }}"
      >>{{category.name}}</router-link>
    </div>
  </aside>
</template>
<script>
import AxiosClass from "@/config/axiosClass.js";
export default {
  data() {
    return {
      catrgoryData: []
    };
  },
  methods: {
    getCategory() {
      const self = this;
      let get = new AxiosClass(
        `category_gender/${this.$route.params.gender}`,
        "get"
      );
      get.doAxios(
        self,
        res => {
          this.catrgoryData = res.data.data;
        },
        res => {
          console.log("fail", res.data);
        }
      );
    }
  },
  created() {
    this.getCategory();
  }
};
</script>
<style lang="scss" scoped>
* {
  font-size: 12px;
}
.category-title {
  color: #555555;
}
</style>
