<template>
  <article v-if="!isLoading">
    <img src="@/assets/img/women_banner.jpg" class="w-100 mb-4" alt>
    <div class="row">
      <Card
        class="col-6 col-sm-3 mb-3"
        v-for="(item,index) in productsData"
        :key="index"
        :cardListProp="item"
      />
    </div>
  </article>
</template>
<script>
import Card from "./cardList_singleCard.vue";
import AxiosClass from "@/config/axiosClass.js";
export default {
  name: "cardList",
  components: {
    Card
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    }
  },
  data() {
    return {
      productsData: []
    };
  },
  methods: {
    testGet() {},
    getProductList: function(gender, category ='all') {
      const self = this;
      const getProductList = new AxiosClass(
        `product/${gender}/${category}`,
        "get"
      );
      getProductList.doAxios(
        self,
        res => {
          self.productsData = res.data.data;
        },
        res => {
          console.log("失敗", res);
        }
      );
    }
  },
  beforeUpdate(){
    console.log('beforeUpdate')
  },

  created() {
    this.$store.commit("LOADING", true);
    console.log(this.$route.params.category)
    this.getProductList(
      this.$route.params.gender,this.$route.params.category
    );
  }
};
</script>